import {map, comp, transduce, push, drop} from "../_snowpack/pkg/@thi.ng/transducers.js";
import {EV_TOGGLE_VALUE} from "../_snowpack/pkg/@thi.ng/interceptors.js";
import {link as link_, mergeAttribs} from "../_snowpack/pkg/@thi.ng/hdom-components.js";
import {CLOSE, MENU} from "../_snowpack/pkg/@thi.ng/hiccup-carbon-icons.js";
import {ROUTES} from "./config.js";
import {routeTo, fmtDate} from "./utils.js";
export const link = ({ui}, attribs, href, body) => link_({href, ...mergeAttribs(ui.link.default, attribs)}, body);
export const image = {
  init(el, __, _, src) {
    const img = new Image();
    img.src = src;
    img.onload = () => el.src = src;
  },
  render({ui}, attribs) {
    return [
      "img",
      {
        src: "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNgYAAAAAMAASsJTYQAAAAASUVORK5CYII=",
        ...mergeAttribs(ui.image.loading, attribs)
      }
    ];
  },
  release() {
  }
};
export const routeLink = (ctx, routeID, routeParams, attribs, body) => [
  "a",
  {
    ...mergeAttribs(ctx.ui.link.default, attribs),
    onclick: (e) => {
      e.preventDefault();
      routeTo(ctx.bus, routeID, routeParams);
    }
  },
  body
];
const itemThumbTitle = (ctx, item) => [
  "div",
  ctx.ui.item.thumb.title,
  [routeLink, ROUTES.ITEM, {id: item.id}, null, item.title]
];
const itemTags = (ctx, tags) => {
  const elms = [];
  for (const tag of tags) {
    elms.push(["span", ctx.ui.tag.sign, "#"]);
    elms.push(["span", ctx.ui.tag.normal, tag.name]);
    elms.push(["span", ", "]);
  }
  elms.pop();
  return ["div", elms];
};
const asset = (type, name) => `/assets/${type}/${name}`;
const itemCover = (ctx, item) => [
  "div",
  {
    ...ctx.ui.item.thumb.cover,
    onclick: (e) => {
      e.preventDefault();
      routeTo(ctx.bus, ROUTES.ITEM, {id: item.id});
    }
  },
  [
    image,
    ctx.ui.item.thumb.image,
    asset("images", `${item.id}/${item.images[0].name}`)
  ]
];
const itemThumb = (ctx, item) => [
  "div",
  ctx.ui.item.thumb.main,
  [itemCover, item],
  [
    "div",
    ctx.ui.item.thumb.content,
    ["div", ctx.ui.item.thumb.date, fmtDate(item.date)],
    [itemThumbTitle, item],
    [itemTags, item.tags]
  ]
];
export const featuredItemList = (ctx, items) => [
  "div",
  ctx.ui.item.list,
  map((x) => [itemThumb, x], items)
];
const itemHeader = (ctx, item) => [
  "div",
  ctx.ui.item.full.header,
  ["div", ctx.ui.item.full.date, fmtDate(item.date)],
  ["h1", ctx.ui.item.full.title, item.title],
  [itemTags, item.tags]
];
const itemImage = (ctx, item, img) => [
  image,
  ctx.ui.item.full.image,
  asset("images", `${item.id}/${img.name}`),
  [img.width, img.height]
];
export const itemFull = (ctx, item) => {
  const images = transduce(comp(drop(1), map((img) => ["p", [itemImage, item, img]])), push(), item.images);
  return [
    "div",
    {key: item.id, ...ctx.ui.item.full.main},
    [itemHeader, item],
    [
      "div",
      ctx.ui.item.full.content,
      ["p", [itemImage, item, item.images[0]]],
      ["p", item.description],
      ["p", [link, ctx.ui.link.external, item.url, "[more]"]],
      ...images
    ]
  ];
};
const title = (ctx) => [
  routeLink,
  ROUTES.HOME,
  null,
  null,
  ["h1", ctx.ui.title, "Zihou Ng"]
];
const navToggle = (ctx) => {
  const navVisible = ctx.views.nav.deref().visible;
  const attribs = {
    ...ctx.ui.nav.button,
    onclick: () => ctx.bus.dispatch([EV_TOGGLE_VALUE, "nav.visible"])
  };
  return ["div", attribs, navVisible ? CLOSE : MENU];
};
const header = (ctx) => ["div", ctx.ui.header, title, navToggle];
const itemSlim = (ctx, item) => [
  "div",
  ctx.ui.item.slim.main,
  ["div", ctx.ui.item.slim.date, fmtDate(item.date)],
  [
    "div",
    ctx.ui.item.slim.body,
    [
      routeLink,
      ROUTES.ITEM,
      {id: item.id},
      ctx.ui.item.slim.title,
      item.title
    ],
    [itemTags, item.tags]
  ]
];
const itemList = (ctx) => {
  const items = ctx.views.items.deref();
  return ["div", map((item) => [itemSlim, item], items)];
};
const socialLink = (ctx, url, name) => [
  link,
  ctx.ui.link.external,
  url,
  name
];
const nav = (ctx) => [
  "div",
  [
    "div",
    ctx.ui.nav.links,
    ["div", [socialLink, "https://github.com/stwind", "github"]],
    ["div", [socialLink, "https://observablehq.com/@stwind", "observable"]],
    ["div", [socialLink, "https://qiita.com/stwind", "qiita"]],
    ["div", ctx.ui.email, "stwindfy#gmail dot com"]
  ],
  [itemList]
];
export const app = (ctx) => {
  const navVisible = ctx.views.nav.deref().visible;
  return [
    "div",
    ctx.ui.app,
    header,
    ["div", ctx.ui.profile, "Software Engineer"],
    ["div", ctx.ui.content, navVisible ? nav : ctx.views.content]
  ];
};
