import * as __SNOWPACK_ENV__ from '../_snowpack/env.js';
import.meta.env = __SNOWPACK_ENV__;

import {start} from "../_snowpack/pkg/@thi.ng/hdom.js";
import {Atom} from "../_snowpack/pkg/@thi.ng/atom.js";
import {EventBus} from "../_snowpack/pkg/@thi.ng/interceptors.js";
import {HTMLRouter, EVENT_ROUTE_CHANGED} from "../_snowpack/pkg/@thi.ng/router.js";
import {
  routes,
  initialState,
  ui,
  events,
  views,
  EV,
  FX,
  ROUTES
} from "./config.js";
import {app} from "./components.js";
import {makeViews, routeTo} from "./utils.js";
const state = new Atom(initialState);
const bus = new EventBus(state, events);
const router = new HTMLRouter({
  useFragment: true,
  defaultRouteID: ROUTES.HOME,
  routes
});
router.addListener(EVENT_ROUTE_CHANGED, (e) => console.log(e.value));
router.addListener(EVENT_ROUTE_CHANGED, (e) => bus.dispatch([EVENT_ROUTE_CHANGED, e.value]));
bus.addEffect(FX.ROUTE_TO, ([id, params]) => router.routeTo(router.format(id, params)));
const ctx = {state, bus, ui, views: makeViews(state, views)};
const stop = start(({bus: bus2}) => bus2.processQueue() ? app : null, {ctx});
bus.dispatch([EV.FETCH_DATA]);
router.start();
export function refresh(route) {
  const {id, params} = route;
  routeTo(bus, id, params);
}
const hot = undefined /* [snowpack] import.meta.hot */ ;
if (hot) {
  hot.accept(({module}) => {
    module.refresh(hot.current);
  });
  hot.dispose(() => {
    hot.current = router.current;
    stop();
    router.release();
  });
}
